#content{display:flex;flex-direction:column;align-items:center;text-align:center;padding:40px 0}#content h1{font-size:1.5rem;margin-bottom:20px;position:relative;font-weight:bold}#content h1::after{content:"";display:block;width:50px;height:3px;background-color:#56397b;margin:10px auto 0}#content .content{padding-top:50px}#content .imageWrapper{position:relative}#content .circle{position:absolute;left:50%;transform:translateX(-50%);z-index:-1;height:360px;width:300px;padding-top:20px}#content .teacher{position:absolute;top:-6px;left:50%;transform:translateX(-50%);width:280px;height:360px;object-fit:contain;z-index:3}#content .button{background-color:#56397b;color:#fff;padding:16px 50px;border-radius:5px;cursor:pointer;border:none;font-size:1rem;cursor:pointer;transition:background-color .3s;margin-top:348px;z-index:3}#content .button:hover{background-color:#4a2f6d;color:#fff}